.form_heading {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 20px;
  text-transform: uppercase;
  text-decoration: underline;
  text-align: center;
}

.form_container {
  width: auto;
  height: auto;
  padding: 30px;
  background-color: white;
}

.select {
  width: 262px !important;
}

.select_error {
  border-color: red !important;
}

.input,
.resendBtn,
.select {
  width: 250px;
  height: 35px;
  outline: none;
  border: 1px solid black;
  border-radius: 2px;
  padding: 0 5px;
  margin: 5px 0;
  font-size: 15px;
}

.error {
  border-color: red !important;
}
.error_msg {
  font-size: 12px;
  width: 250px;
  color: red;
}

.form_image {
  width: 500px;
  height: 500px;
}

/* Email verify and resend styles */
.email_container {
  width: 400px;
  height: 300px;
  background-color: white;
}

.resendBtn {
  width: 60% !important;
  border: none !important;
  background: var(--background);
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  margin: 30px 0 0 0;
  cursor: pointer;
}

/* profile page styles */

.profile_wraper {
  display: flex;
  justify-content: center;
}

.profile_options {
  margin-top: 50px;
  width: 150px;
  height: 300px;
  border-top: 1px solid black;
  border-right: 1px solid black;
}

.profile_container {
  margin-top: 50px;
  width: auto;
  min-width: 500px;
  min-height: 300px;
  border-top: 1px solid black;
}

.profile_btn {
  display: flex;
  justify-content: left;
  align-items: center;
  width: 90%;
  height: 50px;
  font-size: 15px;
  color: black;
  font-weight: bold;
  border-bottom: 1px solid black;
  text-transform: capitalize;
  cursor: pointer;
}

.profile_data {
  width: 100%;
  min-height: 300px;
  background: white;
  box-shadow: var(--box-shadow);
  padding: 2%;
  margin: 2%;
}

.profile_details {
  display: flex;
  flex-flow: wrap;
  flex-direction: column;
  justify-content: center;
}

.profile_option {
  display: flex;
  align-items: center;
}

.profile_label {
  text-transform: capitalize;
  flex: 0 0 25%;
}

/* mobile device styles */
@media only screen and (max-width: 600px) {
  .form_image {
    display: none;
  }

  .profile_container {
    min-width: 300px !important;
    width: 300px !important;
    border: none;
  }

  .profile_data {
    margin: 0 !important;
  }
}
