.container {
  width: 100vw;
  height: 100vh;
  background: var(--background);
}

.active {
  color: var(--blue) !important;
}

.icon {
  width: 30px;
  height: 30px;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.space_around {
  justify-content: space-around !important;
}

.row {
  flex-direction: row;
}

.column {
  flex-direction: column;
}

.text_center {
  text-align: center;
}

.remove_a {
  text-decoration: none !important;
}
